module.exports = {
	onShow() {
		let pages = getCurrentPages();
		if (pages.length >= 1) {
			this.$x.pageRouter = (pages[pages.length - 1]).route 
			if(this.$x.pageRouter){
				this.$x.mpShare = {
					title: '', // 
					path: '/pages/index/index', // 
					imageUrl: '' // 
				}
			}
			return ;
		}		
	},
	// 
	onShareAppMessage(el) {
		return this.$x.mpShare
	},
	// 
	onShareTimeline(){
		return this.$x.mpShare
	}
}
